import React, { useEffect, useState } from "react";
import { gsap } from "gsap";
import Logo from "../components/Logo";
import { noise } from "../scripts/noise-machine";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import AniLink from "gatsby-plugin-transition-link/AniLink";
import { Helmet } from "react-helmet";
import socials from "../data/socials";
import useLinkCursor from "../hooks/useLinkCursor";

export default function NavWrapper({ children }) {
  const [drawerOpened, toggleDrawerOpened] = useState(false);
  const ntl = gsap.timeline();
  useLinkCursor();
  useEffect(() => {
    noise();
    let posX = 0,
      posY = 0,
      mouseX = 0,
      mouseY = 0;
    gsap.to("body", {
      duration: 0.01,
      repeat: -1,
      onRepeat: function () {
        posX += (mouseX - posX) / 9;
        posY += (mouseY - posY) / 9;
        gsap.set("#follow", {
          css: {
            left: posX - 37,
            top: posY - 37,
          },
        });
        gsap.set("#cursor", {
          css: {
            left: mouseX,
            top: mouseY,
          },
        });
      },
    });
    document.addEventListener("mousemove", function (e) {
      mouseX = e.pageX;
      mouseY = e.pageY;
    });
    window.onresize = () => {
      noise();
    };
  }, []);

  function toggleDrawer() {
    if (!drawerOpened) {
      ntl
        .set(".ham-menu", { css: { display: "flex" } })
        .to(".ham-menu", {
          duration: 0.4,
          css: { height: "100%" },
        })
        .to(".ham-menu a", {
          duration: 0.4,
          x: 0,
        })
        .to(".socials", {
          autoAlpha: 1,
          duration: 0.2,
          y: 0,
          ease: "bounce.inOut",
        });
    } else {
      ntl
        .to(".ham-menu a:nth-of-type(odd)", {
          duration: 0.4,
          x: "-100vw",
        })
        .to(
          ".ham-menu a:nth-of-type(even)",
          {
            duration: 0.4,
            x: "100vw",
          },
          "-=0.4"
        )
        .to(
          ".socials",
          {
            duration: 0.4,
            y: "10vh",
            autoAlpha: 0,
          },
          "-=0.4"
        )
        .to(".ham-menu", {
          duration: 0.4,
          css: { height: 0, display: "none" },
        });
    }
    toggleDrawerOpened(!drawerOpened);
  }
  function closeDrawer() {
    if (drawerOpened) {
      ntl
        .to(".ham-menu a:nth-of-type(odd)", {
          duration: 0.4,
          x: "-100vw",
        })
        .to(
          ".ham-menu a:nth-of-type(even)",
          {
            duration: 0.4,
            x: "100vw",
          },
          "-=0.4"
        )
        .to(
          ".socials",
          {
            duration: 0.4,
            y: "10vh",
            autoAlpha: 0,
          },
          "-=0.4"
        )
        .to(".ham-menu", {
          duration: 0.4,
          css: { height: 0, display: "none" },
        });
      toggleDrawerOpened(false);
    }
  }
  return (
    <>
      <Helmet>
        <title>Shakil Ahmmed</title>
        <meta
          name="description"
          content="Shakil Ahmmed, Fullstack Web and Mobile Application Developer with over 4 years of experience."
        />
        <meta
          name="keywords"
          content="developer,portfolio,web application developer,mobile application developer,react developer,react native developer"
        />
        <meta name="author" content="Shakil Ahmmed" />
        <meta name="copyright" content="Shakil Ahmmed" />
        <meta name="robots" content="index,follow" />
        <meta property="og:title" content="Shakil Ahmmed" />
        <meta property="og:site_name" content="Shakil Ahmmed" />
        <meta property="og:url" content="https://shakil.info" />
        <meta
          property="og:description"
          content="Shakil Ahmmed, Fullstack Web and Mobile Application Developer with over 4 years of experience."
        />
        <meta property="og:type" content="profile" />
        <meta
          property="og:image"
          content="https://shakil.info/shakil-ahmmed.jpg"
        />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:site" content="@ShakilNotFound" />
        <meta name="twitter:title" content="Shakil Ahmmed" />
        <meta
          name="twitter:description"
          content="Shakil Ahmmed, Fullstack Web and Mobile Application Developer with over 4 years of experience."
        />
        <meta
          name="twitter:image"
          content="https://shakil.info/shakil-ahmmed.jpg"
        />
      </Helmet>
      <div id="cursor"></div>
      <div id="follow"></div>
      <div className="primary">
        <canvas id="noise" className="noise"></canvas>
        <div className="nav-container">
          <div className="content">
            <div className="content-inner">
              <div className={`ham-menu ${drawerOpened ? "opened" : ""}`}>
                <nav className="nav-menu">
                  <AniLink
                    paintDrip
                    duration={0.6}
                    to="/"
                    hex="#252525"
                    activeClassName="active"
                    onClick={closeDrawer}
                  >
                    Home
                  </AniLink>
                  <AniLink
                    paintDrip
                    duration={0.6}
                    to="/works"
                    hex="#252525"
                    activeClassName="active"
                    onClick={closeDrawer}
                  >
                    Works
                  </AniLink>
                  <AniLink
                    paintDrip
                    duration={0.6}
                    to="/contact"
                    hex="#252525"
                    activeClassName="active"
                    onClick={closeDrawer}
                  >
                    Contact
                  </AniLink>
                </nav>
                <div className="socials">
                  {socials.map(({ a, icon }) => (
                    <a
                      key={a.title}
                      href={a.href}
                      title={a.title}
                      rel="noopener noreferrer"
                      target="_blank"
                    >
                      <span>
                        <FontAwesomeIcon icon={icon} />
                      </span>
                    </a>
                  ))}
                </div>
              </div>
              {children}
            </div>
          </div>
          <AniLink
            paintDrip
            duration={0.6}
            to="/"
            id="logo"
            hex="#252525"
            activeClassName="active"
            onClick={closeDrawer}
          >
            <Logo />
          </AniLink>
          <div className="title">Shakil Ahmmed</div>
          <div className={`ham`}>
            <button
              className={`hamburger hamburger--spring ${
                drawerOpened ? "is-active" : ""
              }`}
              onClick={toggleDrawer}
            >
              <div className="hamburger-box">
                <div className="hamburger-inner"></div>
              </div>
            </button>
          </div>
          <AniLink
            paintDrip
            duration={0.6}
            to="/works"
            hex="#252525"
            className="works"
            activeClassName="active"
            onClick={closeDrawer}
          >
            Works
          </AniLink>
          <AniLink
            paintDrip
            duration={0.6}
            to="/contact"
            hex="#252525"
            className="contact"
            activeClassName="active"
            onClick={closeDrawer}
          >
            Contact
          </AniLink>
        </div>
      </div>
    </>
  );
}
